.NavList {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.NavListItem {
  cursor: pointer;
  margin-bottom: 30px;
}

.NavListItem:hover,
.NavListItem:active,
.NavListItem:focus {
  color: #00a9ef;
}

.NavListItemLink {
  font-family: "IBMPlexSans-Bold", sans-serif;
  font-size: 24px;
  margin-bottom: 8px;
  text-transform: uppercase;
}

.NavListItemDescription {
  font-family: "IBMPlexSans-Regular";
  font-size: 14px;
  margin: 0;
  text-transform: none;
}

.Link {
  color: #444;
  text-decoration: none;
}
